import { MainLayout } from '../../layouts/MainLayout';
import styled from 'styled-components/macro';
import { AiOutlineUser, AiFillEye, AiFillEyeInvisible } from 'react-icons/ai';
import { RiLockPasswordLine } from 'react-icons/ri';
import { ImNotification } from 'react-icons/im';
import { useState } from 'react';
import { useHistory } from 'react-router-dom';
const RegisterPageContainer = styled.div`
  max-width: 400px;
  margin: 150px auto;
`;
const TabContainer = styled.div`
  display: flex;
  align-items: center;
  .login {
    padding: 15px 0;
    width: 50%;
    border-radius: 10px 0 0 0;
    background-color: rgb(162, 181, 159, 0.4);
    color: black;
    text-align: center;
    cursor: pointer;
  }
  .register {
    padding: 15px 0;
    width: 50%;
    border-radius: 10px 0 0 0;
    background-color: #fff;
    color: black;
    text-align: center;
  }
`;
const FormContainer = styled.form`
  margin-bottom: 20px;
  padding: 20px 0;
  background-color: #fff;

  .input_container {
    position: relative;
    padding: 10px 30px;
    margin: 10px 0;
    .user_input {
      width: 100%;
      padding: 5px 10px 5px 40px;
      height: 45px;
      border: solid 1px transparent;
      border-radius: 3px;
      font-size: 16px;
      color: #333;
      background-color: #a2b59f;
      transition: 0.3s;
      ::placeholder {
        color: #fff;
        opacity: 0.5;
      }
    }
    .nickname_input {
      width: 100%;
      padding: 5px 10px 5px 40px;
      height: 45px;
      border: solid 1px transparent;
      border-radius: 3px;
      font-size: 16px;
      color: #333;
      background-color: #a2b59f;
      transition: 0.3s;
      ::placeholder {
        color: #fff;
        opacity: 0.5;
      }
    }
    .password_input {
      position: relative;
      width: 100%;
      padding: 5px 10px 5px 40px;
      height: 45px;
      border: solid 1px transparent;
      border-radius: 3px;
      font-size: 16px;
      color: #333;
      background-color: #a2b59f;
      transition: 0.3s;
      ::placeholder {
        color: #fff;
        opacity: 0.5;
      }
    }

    .show_password_icon {
      cursor: pointer;
      position: absolute;
      right: 10%;
      top: 50%;
      transform: translateY(-50%);
      font-size: 18px;
    }
  }
  .notice {
    margin-bottom: 10px;
    padding: 5px 10px 5px 40px;
    color: #333;
    span {
      margin-left: 10px;
    }
  }
  .submit {
    margin: 10px 30px;
    .submit_button {
      cursor: pointer;
      width: 100%;
      padding: 12px 0;
      text-align: center;
      letter-spacing: 0.3em;
      color: #fff;
      background-color: teal;
      border: transparent;
      border-radius: 3px;
      font-size: 16px;
      cursor: pointer;
      transition: 0.3s;
      &:hover {
        opacity: 0.9;
      }
    }
  }
`;
export const RegisterPage = () => {
  const [isShowPassword, setIsShowPassword] = useState();
  const history = useHistory();

  //
  const handleChangeTabLogin = () => {
    history.push('/login');
  };

  //
  const handleChangeTabRegister = () => {
    history.push('/register');
  };

  // 
  const handleShowPassword = () => {
    setIsShowPassword(!isShowPassword);
  };
  return (
    <MainLayout>
      <RegisterPageContainer>
        <TabContainer>
          <div className="login" onClick={handleChangeTabLogin}>
            
          </div>
          <div className="register" onClick={handleChangeTabRegister}>
            
          </div>
        </TabContainer>
        <FormContainer>
          <div className="input_container">
            <input
              type="text"
              name="username"
              placeholder=""
              className="user_input"
            />
          </div>

          <div className="input_container">
            <input
              type="text"
              name="nickname"
              placeholder=""
              className="nickname_input"
            />
          </div>

          <div className="input_container">
            <input
              type={isShowPassword ? 'text' : 'password'}
              name="password"
              placeholder=""
              className="password_input"
            />
            <div onClick={handleShowPassword}>
              {isShowPassword ? (
                <AiFillEye className="show_password_icon" />
              ) : (
                <AiFillEyeInvisible className="show_password_icon" />
              )}
            </div>
          </div>
          <div className="notice">
            <ImNotification />
            <span></span>
          </div>
          <div className="submit">
            <div className="submit_button"></div>
          </div>
        </FormContainer>
      </RegisterPageContainer>
    </MainLayout>
  );
};
